.main-header__toggle {
  position: relative;
  display: block;
  width: 24px;
  height: 16px;
  margin-left: auto;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  cursor: pointer;
}
.main-header__toggle::before, .main-header__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s ease 0s;
}
.main-header__toggle::before {
  top: -1px;
}
.main-header__toggle::after {
  bottom: -1px;
}
.no-js .main-header__toggle {
  display: none;
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:focus-visible {
  outline: 1px solid #000000;
  outline-offset: 2px;
}
.main-header__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle-icon {
  position: absolute;
  top: 7px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s ease 0s;
}

.is-open .main-header__toggle::before {
  top: 7px;
  transform: rotate(45deg);
}

.is-open .main-header__toggle::after {
  bottom: 7px;
  transform: rotate(-45deg);
}

.is-open .main-header__toggle-icon {
  height: 0;
}

.main-nav {
  position: absolute;
  top: 65px;
  left: -20px;
  display: none;
  width: calc(100% + 40px);
